/** @format */

.FormBilling {
  width: 100%;
  font-family: Roboto;
  text-align: center;
  margin-bottom: 50px;
}

.Logo_Section {
  padding: 40px;
  width: 100%;
}

.Logo_Section svg {
  margin: auto;
}

.Form_Header {
  padding-top: 32px;
  display: grid;
  grid-template-columns: 15% 80%;
}

.Form_Header .Back_Btn {
  align-self: center;
}

.Form_Header .Header_Text h1 {
  text-align: start;
  font-size: 24px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.17;
  letter-spacing: normal;
  color: #3b414a;
}

main form div {
  position: relative;
  width: 80%;
  margin: 30px auto;
  display: grid;
  grid-template-columns: 1fr;
}

main .Form form button {
  margin-left: 10%;
  display: flex;
  justify-content: space-between;
  border: none;
  outline: none;
  padding: 15px 20px 15px 20px;
  border-radius: 8px;
  box-shadow: 0 8px 8px 0 rgba(50, 197, 255, 0.1);
  background-color: #12b0ee;
  font-family: Roboto;
  font-size: 16px;
  font-weight: bold;
  color: #ffffff;
}

main .Form form button svg {
  margin-left: 10px;
}
